<!-- eslint-disable vue/multi-word-component-names -->
<template>
    <div class="main">
      <h1>{{ msg }}</h1>
      <img class="imgj" src="../assets/jesus.jpg" alt="Jesus DAVID " />
      <section>
        <h2>Podras registrar cualquier pago que tengas</h2>
        <ul class="intro">
            <li>Para ingresar monto que debas, ingresa el numero en positivo</li>
            <li class="ejemplito">Ejemplo de ingreso "Descripción" :"Pago de luz" "Monto" : "40000"</li>
            <li>Para ingresar monto que ya pagaste, ingresa el numero en negativo</li>
            <li class="ejemplito">Ejemplo de ingreso "Descripción" :"Pago de luz realizado" "Monto" : "-40000"</li>
        </ul>
        <section class="regispago">
            <h2>Registrar Pago</h2>
            <form @submit.prevent="addPayment">
                <input type="text" v-model="payment.description" placeholder="Descripción" required>
                <input type="number" v-model="payment.amount" placeholder="Monto" required>
                <button type="submit">Añadir</button>
            </form>
        </section>

      </section>
      <section>
        <h2>Lista de Pagos</h2>
        <ul>
          <li v-for="item in payments" :key="item.id">
            {{ item.description }} - {{ formatCurrency(item.amount) }}
          </li>
        </ul>
      </section>
      <section class="totalP">
        <h2>Total Pagos:</h2>
        <span>{{ formatCurrency(totalPayments) }}</span>
      </section>
    </div>


    


  </template>
  
  <script>
  export default {
    name: 'HelloWorld',
    props: {
      msg: String
    },
    data() {
      return {
        payment: {
          description: '',
          amount: 0
        },
        payments: []
      };
    },
    computed: {
      totalPayments() {
        return this.payments.reduce((sum, payment) => sum + parseFloat(payment.amount), 0);
      }
    },
    methods: {
      addPayment() {
        this.payments.push({

          description: this.payment.description,
          amount: parseFloat(this.payment.amount),
      
        });
        this.payment.description = '';
        this.payment.amount = 0;
    
      },
      formatCurrency(value) {
        return '$' + parseFloat(value).toFixed(2);
      },
    }
  }
  </script>
  
  <style scoped>
  .main{
   
    margin: 0px;
    padding: 0px;
  }
  body{
    margin: 0px;
    padding: 0px;

  }
.regispago{
    
}
  .intro {
    font-style: oblique;
  }
  .ejemplito{
    font-size: small;
    color: red;
  }
  h2 {
    margin-top: 20px;
  }
  form {
    display: flex;
    flex-direction: column;
  }
  input, button {
    margin: 9px ;
    padding: 8px;
    font-size: 1em;
  }
  ul {
    list-style-type: none;
  }
  li {
    padding: 10px ;
  }
  .totalP {
    display: flex;
    justify-content: space-between;
    margin: 20px ;
  }
  .imgj{
    max-width: 500px;
  }
  @media (max-width: 600px) {
    .imgj {
        width: 200px;
    }
  }
  @media (max-width: 255px) {
    .imgj {
        display: none;
    }
  }
  </style>
  